@import url(https://fonts.googleapis.com/css?family=Ubuntu+Mono&display=swap);
.link {
  font-family: Ubuntu Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  display: flex !important;
  align-items: center;
  color: #000000 !important; }

.App {
  text-align: center; }

.App-logo {
  height: 40vmin; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #09d3ac; }

h1, h2 {
  margin-bottom: 16px; }

.app-input .secondary-button {
  margin-top: 8px; }

.app-input-group {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  margin-bottom: 24px; }
  .app-input-group .app-input:not(:first-child) {
    margin-top: 12px; }

.app-input {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start; }
  .app-input label {
    font-family: "Ubuntu Mono", monospace;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 16px;
    color: #000000;
    opacity: 0.4;
    margin-bottom: 4px; }
  .app-input input {
    width: 100%;
    border: 0;
    height: 32px;
    padding-left: 8px;
    padding-right: 8px;
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    font-family: "Ubuntu Mono", monospace;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: #000000; }
    .app-input input:focus {
      outline: none;
      background-color: rgba(0, 0, 0, 0.15); }

.wide-input {
  width: 30%;
  min-width: 350px;
  max-width: 500px; }

.medium-input {
  min-width: 250px;
  max-width: 400px;
  width: 22%; }

.small-input {
  min-width: 150px;
  max-width: 250px;
  width: 14%; }

.tiny-input {
  min-width: 100px;
  max-width: 150px;
  width: 9%; }

.primary-button {
  border: none;
  height: 32px;
  border-radius: 4px;
  padding-left: 8px;
  padding-right: 8px;
  background: #F4C52E;
  font-family: "Ubuntu Mono", monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 16px;
  text-align: center;
  color: #000000; }

.secondary-button {
  background: #FFFFFF;
  border: 0.5px solid #1AA5B8;
  box-sizing: border-box;
  border-radius: 4px;
  height: 32px;
  padding-left: 8px;
  padding-right: 8px;
  font-family: "Ubuntu Mono", monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 16px;
  text-align: center;
  color: #1AA5B8; }

.warn-button {
  background: #FFFFFF;
  border: 0.5px solid #FF5550;
  box-sizing: border-box;
  border-radius: 4px;
  height: 24px;
  padding-left: 8px;
  padding-right: 8px;
  font-family: "Ubuntu Mono", monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 16px;
  text-align: center;
  color: #FF5550; }

.small-button {
  height: 24px !important; }

.app-button-group {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center; }
  .app-button-group button:first-child {
    margin-right: 16px; }

.page-content {
  text-align: left;
  width: 100%;
  padding-left: 24px;
  padding-top: 16px;
  padding-bottom: 40px; }

h1 {
  font-family: "Ubuntu Mono", monospace;
  font-weight: bold;
  font-size: 24px;
  line-height: 24px;
  color: #000000; }

.balance-value {
  font-family: "Ubuntu Mono", monospace;
  font-weight: bold;
  font-size: 40px;
  line-height: 40px;
  color: #000000; }

.regular-text {
  font-family: "Ubuntu Mono", monospace;
  font-weight: normal;
  font-size: 16px;
  line-height: 16px;
  color: #000000; }

h2 {
  font-family: "Ubuntu Mono", monospace;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 16px;
  color: #000000; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

